.Profile-Box-Spacing {
    display: inline-block;
    padding-left: 1.5%;
    padding-right: 1%;
    padding-bottom: 10px;
    width: 30%;
    min-width: 275px;
}

.Search-Window-Container {
    width: 75%;
    background-color: #EAEBEC;
    height: 100%;
    padding-top: 10px;
    min-width: 270px;
    display: inline-block;
    float: right;
    min-height: 86vh;
    text-align: center;
}

.Search-Form {
    border-radius: 20px;
    border: 1px solid #007EFF;
    width: 35%;
    padding-bottom: 5px;
    padding-right: 5px;
    padding-top: 5px;
    margin-bottom: 5px;
    margin-left: 10px;
    float: left;
    background-color: white;
}

.Search-Box {
    font-size: 16px;
}

.Search-input {
    width: 75%;
    font-size: 16px;
    vertical-align: middle;
    border: none;
    margin-right: 5px;
    border-bottom: 1px solid black;
}

.Search-input:focus {
    outline: none;
}

.AZsort {
    border: none;
    color: blue;
    background-color: inherit;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    font-size: 18px;
}

.search-btn {
    display: inline;
    width: 20px;
    float: right;
}

.search-btn-container {
    border-radius: 15px;
    background-color: white;
    border: none;
    float: right;
}

.search-area{
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: solid grey 1px;
}

.Dropdown-root {
    position: relative;
    width: 20%;
    display: inline-block;
}

.Dropdown-control {
    position: relative;
    overflow: hidden;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 2px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: #333;
    cursor: default;
    outline: none;
    padding: 8px 52px 8px 10px;
    -webkit-transition: all 200ms ease;
    -o-transition: all 200ms ease;
    transition: all 200ms ease;
}

.Dropdown-control:hover {
    -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
            box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
}

.Dropdown-arrow {
    border-color: #999 transparent transparent;
    border-style: solid;
    border-width: 5px 5px 0;
    content: ' ';
    display: block;
    height: 0;
    margin-top: -ceil(2.5);
    position: absolute;
    right: 10px;
    top: 14px;
    width: 0
}

.is-open .Dropdown-arrow {
    border-color: transparent transparent #999;
    border-width: 0 5px 5px;
}

.Dropdown-menu {
    background-color: white;
    border: 1px solid #ccc;
    -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
            box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-top: -1px;
    max-height: 200px;
    overflow-y: auto;
    position: absolute;
    top: 100%;
    width: 100%;
    z-index: 1000;
    -webkit-overflow-scrolling: touch;
}

.Dropdown-menu .Dropdown-group > .Dropdown-title {
    padding: 8px 10px;
    color: rgba(51, 51, 51, 1.2);
    font-weight: bold;
    text-transform: capitalize;
}

.Dropdown-option {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: rgba(51, 51, 51, 0.8);
    cursor: pointer;
    display: block;
    padding: 8px 10px;
}

.Dropdown-option:last-child {
    border-bottom-right-radius: 2px;
    border-bottom-left-radius: 2px;
}

.Dropdown-option:hover {
    background-color: #f2f9fc;
    color: #333;
}

.Dropdown-option.is-selected {
    background-color: #f2f9fc;
    color: #333;
}

.Dropdown-noresults {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: #ccc;
    cursor: default;
    display: block;
    padding: 8px 10px;
}


